import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link, useHistory } from "react-router-dom";

import Logoi from "../../asset/images/logo";
import Facei from "../../asset/images/face";
import Cardi from "../../asset/images/card";
import Menui from "../../asset/images/menu";
import Search from "../../asset/images/search";
import Card from "./card/card";
import Face from "./face/face";
import Menu from "./menu/menu";

import Signin from "../signIn";
import { Login } from "../../Actions";

export default function Nav(props) {
  const rel = {
    position: "relative",
  };
  const fix = {
    position: "fixed",
  };

  const dispatch = useDispatch();
  const history = useHistory();

  if (getCookie("_SSD")) {
    dispatch(Login());
  }

  const login = useSelector((state) => state.login);

  const [search, setSearch] = useState(props.search);
  const [show, setShow] = useState(false);
  const [showCard, setShowCard] = useState(false);
  const [showFace, setShowFace] = useState(false);
  const [showMenu, setShowMenu] = useState(false);

  const closesign = () => {
    setShow(false);
  };

  return (
    <>
      <nav className="fixed" style={props.here ? fix : rel}>
        <div className="logo">
          <Link to="/">
            <Logoi />
          </Link>
        </div>
        <div className="menu">
          <ul>
            <li>
              <div className="search">
                <Search />
                <input
                  type="text"
                  placeholder="Search"
                  value={search}
                  onChange={(e) => {
                    setSearch(e.target.value);
                  }}
                  onKeyDown={(e) => {
                    if (e.keyCode === 13) {
                      history.push("/search/" + search);
                    }
                  }}
                />
              </div>
            </li>
            <li>
              <Link to="/strategys">Strategys</Link>
            </li>
            <li>
              <Link to="/indicators">Indicators</Link>
            </li>
            <li>
              <Link to="/source"> Source </Link>
            </li>
            <li>
              {login ? (
                <a className="withimg" onClick={() => setShowFace(true)}>
                  <Facei />
                </a>
              ) : (
                <a className="signin" onClick={() => setShow(true)}>
                  Sign In
                </a>
              )}
            </li>
            <li>
              <a className="withimg" onClick={() => setShowCard(true)}>
                <Cardi />
              </a>
              <div className="num-card">1</div>
            </li>
          </ul>
        </div>

        <div className="search">
          <Search />
          <input
            type="text"
            placeholder="Search"
            value={search}
            onChange={(e) => {
              setSearch(e.target.value);
            }}
            onKeyDown={(e) => {
              if (e.keyCode === 13) {
                history.push("/search/" + search);
              }
            }}
          />
        </div>

        <div className="media">
          <a onClick={() => setShowCard(true)}>
            <Cardi />
            <div className="num-card">1</div>
          </a>
          <a onClick={() => setShowMenu(true)}>
            <Menui />
          </a>
        </div>
      </nav>

      {showCard ? <Card close={() => setShowCard(false)} /> : <></>}
      {show ? <Signin close={closesign} show={show} /> : <></>}
      {showFace ? <Face close={() => setShowFace(false)} /> : <></>}
      {showMenu ? (
        <Menu close={() => setShowMenu(false)} sign={() => setShow(true)} />
      ) : (
        <></>
      )}
    </>
  );
}

function getCookie(name) {
  var nameEQ = name + "=";
  var ca = document.cookie.split(";");
  for (var i = 0; i < ca.length; i++) {
    var c = ca[i];
    while (c.charAt(0) == " ") c = c.substring(1, c.length);
    if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length, c.length);
  }
  return null;
}
