import React, { useEffect, useState } from "react";
import axios from "axios";
import { useDispatch, useSelector } from "react-redux";
import { Products } from "../../Actions";
import { useLocation, useParams } from "react-router-dom";

import Navbar from "../global/navbar";
import Navfill from "../global/navfil";
import Productlist from "../product/productlist";
import Footer from "../global/footer";

function Products_(props) {
  const dispatch = useDispatch();
  const products = useSelector((state) => state.products);
  const [load, setLoad] = useState(false);

  const { pathname } = useLocation();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  useEffect(() => {
    if (products[0].title === undefined) {
      setLoad(true);
      axios
        .get("/api/product/findall")
        .then(function (response) {
          dispatch(Products(response.data.results));
          setLoad(false);
        })
        .catch(function (error) {
          // handle error
          console.log(error);
        })
        .then(function () {
          // always executed
        });
    }
  }, [products]);

  let { query } = useParams();

  return (
    <>
      <Navbar here={false} search={query} />
      <Navfill />

      {products[0].title !== undefined
        ? products
            .filter((product) => product.type === props.type)
            .map((product) => (
              <Productlist key={product._id} load={load} product={product} />
            ))
        : products.map((product) => (
            <Productlist key={product} load={load} product={product} />
          ))}

      <br />
      <Footer />
    </>
  );
}

export default Products_;
