import { combineReducers } from "redux";
import login from "./login";
import products from "./homeproduct";

const Dark = (state = false, action) => {
  if (localStorage.getItem("DARKLIGHT") === "ON") {
    return true;
  }
  switch (action.type) {
    case "ON":
      return true;
    case "OFF":
      return false;
    default:
      return false;
  }
};

const root = combineReducers({
  login: login,
  Dark,
  products,
});

export default root;
