import React, { useState, useEffect } from "react";
import { useDispatch } from "react-redux";
import { Alert } from "@material-ui/lab";
import axios from "axios";

import { Login } from "../../Actions";

export default function CreateAccount(props) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [conpassword, setConPassword] = useState("");
  const [username, setUsername] = useState("");
  const [error, setError] = useState("");
  const [createClick, setCreateClick] = useState(false);

  const dispatch = useDispatch();

  useEffect(() => {
    if (password === conpassword) {
      setError("");
    }
  }, [conpassword]);

  const diss = (e) => {
    props.dissmis();
  };

  const setemail = (e) => {
    setEmail(e.target.value);
  };

  const setusername = (e) => {
    setUsername(e.target.value);
  };

  const setpassword = (e) => {
    setPassword(e.target.value);
  };

  const setconpassword = (e) => {
    setConPassword(e.target.value);
  };

  const addUser = () => {
    if (password !== conpassword) {
      setError("password not equal");
      return;
    }
    setCreateClick(true);
    setError("");
    axios({
      method: "post",
      url: "/api/user/add",
      data: {
        email,
        password,
        username,
      },
    })
      .then(function (response) {
        const res = response.data.response;
        if (!res.add) {
          setError(res.errors.message);
        } else {
          props.close();
          dispatch(Login());
        }
      })
      .catch(function (error) {
        console.log(error);
        setError("Network Error! Please try again later");
      })
      .finally(function () {
        setCreateClick(false);
      });
  };

  return (
    <div className="contain-createaccount">
      <h3>Create Account</h3>
      <form>
        <input
          type="email"
          name="email"
          placeholder="Email"
          value={email}
          onChange={setemail}
        />
        <input
          type="password"
          name="password"
          placeholder="Password"
          value={password}
          onChange={setpassword}
        />
        <input
          type="password"
          name="confirmPassword"
          placeholder="Confirm password"
          value={conpassword}
          onChange={setconpassword}
        />
        <input
          type="text"
          name="username"
          placeholder="User Name"
          value={username}
          onChange={setusername}
        />
        {/* {error !== "" ? <p className="perror">{error}</p> : <></>} */}
        {error == "" ? <></> : <Alert severity="error">{error}</Alert>}

        <div className="btn-1">
          <a onClick={diss}>I have account</a>
          <a className={createClick ? "aclick" : "btn-login"} onClick={addUser}>
            Register
          </a>
        </div>
      </form>
      <div className="btn-g">
        <a className="btn-gg" href="">
          Continue with google
        </a>
      </div>
    </div>
  );
}
